import type {
  ActionContext,
  CommitOptions,
  DispatchOptions,
  Store as VuexStore,
} from "vuex";
import type { CartProduct } from "@/types/productType";
import type { ApiOrder } from "@/types/productApi";

export interface ProductInterface {
  productId: number;
  amount: number;
}

export interface State {
  cartProducts: Array<ProductInterface>;
  userAccessKey: null | string;
  orderInfo: null | ApiOrder;
  cartLoad: boolean;
  cartLoadFailed: boolean;
  cartProductsData: Array<CartProduct>;
}

export enum MutationTypes {
  RESET_CART = "RESET_CART",
  UPDATE_ORDER_INFO = "UPDATE_ORDER_INFO",
  UPDATE_CART_PRODUCT_AMOUNT = "UPDATE_CART_PRODUCT_AMOUNT",
  DELETE_CART_PRODUCT = "DELETE_CART_PRODUCT",
  UPDATE_USER_ACCESS_KEY = "UPDATE_USER_ACCESS_KEY",
  UPDATE_CART_PRODUCTS_DATA = "UPDATE_CART_PRODUCTS_DATA",
  SYNC_CART_PRODUCTS = "SYNC_CART_PRODUCTS",
}

export enum ActionTypes {
  LOAD_CART = "LOAD_CART",
  LOAD_ORDER_INFO = "LOAD_ORDER_INFO",
  ADD_PRODUCT_TO_CART = "ADD_PRODUCT_TO_CART",
  UPDATE_CART_PRODUCT_AMOUNT = "UPDATE_CART_PRODUCT_AMOUNT",
  DELETE_CART_PRODUCT_AMOUNT = "DELETE_CART_PRODUCT_AMOUNT",
}

export type Mutations<S = State> = {
  [MutationTypes.UPDATE_ORDER_INFO](state: S, payload: ApiOrder): void;
  [MutationTypes.RESET_CART](state: S): void;
  [MutationTypes.UPDATE_CART_PRODUCT_AMOUNT](
    state: S,
    payload: ProductInterface
  ): void;
  [MutationTypes.DELETE_CART_PRODUCT](state: S, productId: number): void;
  [MutationTypes.UPDATE_USER_ACCESS_KEY](state: S, accessKey: string): void;
  [MutationTypes.UPDATE_CART_PRODUCTS_DATA](
    state: S,
    items: Array<CartProduct>
  ): void;
  [MutationTypes.SYNC_CART_PRODUCTS](state: S): void;
};
export type Getters = {
  cartDetailProducts(state: State): Array<CartProduct>;
  cartTotalPrice(
    state: State,
    getters: {
      [K in keyof Getters]: ReturnType<Getters[K]>;
    }
  ): number;
  cartTotalCount(state: State): number;
  isCartLoading(state: State): boolean;
  isCartLoadingFailed(state: State): boolean;
};

export type AugmentedActionContext = {
  commit<K extends keyof Mutations>(
    key: K,
    payload?: Parameters<Mutations[K]>[1]
  ): ReturnType<Mutations[K]>;
} & Omit<ActionContext<State, State>, "commit">;

export interface Actions {
  [ActionTypes.LOAD_ORDER_INFO](
    { commit }: AugmentedActionContext,
    orderId: number
  ): Promise<void>;

  [ActionTypes.LOAD_CART]({
    commit,
    state,
  }: AugmentedActionContext): Promise<void>;

  [ActionTypes.ADD_PRODUCT_TO_CART](
    { commit, state }: AugmentedActionContext,
    payload: ProductInterface
  ): Promise<void>;

  [ActionTypes.UPDATE_CART_PRODUCT_AMOUNT](
    { commit, state }: AugmentedActionContext,
    payload: ProductInterface
  ): Promise<void>;

  [ActionTypes.DELETE_CART_PRODUCT_AMOUNT](
    { commit, state }: AugmentedActionContext,
    productId: number
  ): Promise<void>;
}

export type CustomStore = Omit<
  VuexStore<State>,
  "commit" | "getters" | "dispatch"
> & {
  commit<K extends keyof Mutations, P extends Parameters<Mutations[K]>[1]>(
    key: K,
    payload: P,
    options?: CommitOptions
  ): ReturnType<Mutations[K]>;
} & {
  getters: {
    [K in keyof Getters]: ReturnType<Getters[K]>;
  };
} & {
  dispatch<K extends keyof Actions>(
    key: K,
    payload?: Parameters<Actions[K]>[1],
    options?: DispatchOptions
  ): ReturnType<Actions[K]>;
};
