import type { ActionTree, GetterTree, MutationTree } from "vuex";
import { createLogger, createStore } from "vuex";
import type {
  Actions,
  CustomStore,
  Getters,
  Mutations,
  ProductInterface,
  State,
} from "@/types/store.type";
import { ActionTypes, MutationTypes } from "@/types/store.type";
import type { AxiosResponse } from "axios";
import axios from "axios";
import type { ApiCartProducts, ApiOrder } from "@/types/productApi";
import { API_BASIC_URL } from "@/others/basic-url";
import type { CartProduct } from "@/types/productType";
import { NAMES } from "@/others/names";
import type {
  BasketDeleteProduct,
  BasketProductsConfig,
} from "@/types/configsType";

const state: State = {
  cartProducts: [],
  userAccessKey: null,
  orderInfo: null,
  cartLoad: false,
  cartLoadFailed: false,
  cartProductsData: [],
};

const mutations: MutationTree<State> & Mutations = {
  [MutationTypes.UPDATE_ORDER_INFO](state: State, payload: ApiOrder) {
    state.orderInfo = payload;
  },
  [MutationTypes.RESET_CART](state: State) {
    state.cartProducts.length = 0;
    state.cartProductsData.length = 0;
  },
  [MutationTypes.UPDATE_CART_PRODUCT_AMOUNT](
    state: State,
    payload: ProductInterface
  ) {
    const item = state.cartProducts.find(
      (item) => item.productId === payload.productId
    );
    if (item) {
      item.amount = payload.amount;
    }
  },
  [MutationTypes.DELETE_CART_PRODUCT](state: State, productId: number) {
    state.cartProducts = state.cartProducts.filter(
      (e) => e.productId !== productId
    );
  },
  [MutationTypes.UPDATE_USER_ACCESS_KEY](state: State, accessKey: string) {
    state.userAccessKey = accessKey;
  },
  [MutationTypes.UPDATE_CART_PRODUCTS_DATA](
    state: State,
    items: Array<CartProduct>
  ) {
    state.cartProductsData = items;
  },
  [MutationTypes.SYNC_CART_PRODUCTS](state: State) {
    state.cartProducts = state.cartProductsData.map((item) => ({
      productId: item.product.id,
      amount: item.quantity,
    }));
  },
};

export const actions: ActionTree<State, State> & Actions = {
  [ActionTypes.LOAD_ORDER_INFO]({ commit }, orderId) {
    return axios
      .get<ApiOrder>(`api/orders/${orderId}`, {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then((response) => {
        commit(MutationTypes.UPDATE_ORDER_INFO, response.data);
      });
  },
  [ActionTypes.LOAD_CART]({ commit, state }) {
    state.cartLoad = true;
    state.cartLoadFailed = false;
    return axios
      .get<ApiCartProducts>("api/baskets", {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then((response) => {
        if (!state.userAccessKey) {
          localStorage.setItem(
            NAMES.USER_ACCESS_KEY,
            response.data.user.accessKey
          );
          commit(
            MutationTypes.UPDATE_USER_ACCESS_KEY,
            response.data.user.accessKey
          );
        }
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, response.data.items);
        commit(MutationTypes.SYNC_CART_PRODUCTS);
      })
      .catch(() => {
        state.cartLoadFailed = true;
      })
      .then(() => {
        state.cartLoad = false;
      });
  },
  [ActionTypes.ADD_PRODUCT_TO_CART]({ state, commit }, { productId, amount }) {
    return axios
      .post<
        ApiCartProducts,
        AxiosResponse<ApiCartProducts>,
        BasketProductsConfig
      >(
        "api/baskets/products",
        {
          productId,
          quantity: amount,
        },
        {
          params: { userAccessKey: state.userAccessKey },
          baseURL: API_BASIC_URL,
        }
      )
      .then((response) => {
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, response.data.items);
        commit(MutationTypes.SYNC_CART_PRODUCTS);
      });
  },
  [ActionTypes.UPDATE_CART_PRODUCT_AMOUNT](
    { state, commit },
    { productId, amount }
  ) {
    commit(MutationTypes.UPDATE_CART_PRODUCT_AMOUNT, { productId, amount });
    if (amount < 1) return new Promise((resolve) => resolve(undefined));
    return axios
      .put<
        ApiCartProducts,
        AxiosResponse<ApiCartProducts>,
        BasketProductsConfig
      >(
        "api/baskets/products",
        {
          productId,
          quantity: amount,
        },
        {
          params: { userAccessKey: state.userAccessKey },
          baseURL: API_BASIC_URL,
        }
      )
      .then((response) => {
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, response.data.items);
      })
      .catch(() => commit(MutationTypes.SYNC_CART_PRODUCTS));
  },
  [ActionTypes.DELETE_CART_PRODUCT_AMOUNT]({ state, commit }, productId) {
    commit(MutationTypes.DELETE_CART_PRODUCT, productId);
    return axios
      .delete<
        ApiCartProducts,
        AxiosResponse<ApiCartProducts>,
        BasketDeleteProduct
      >("api/baskets/products", {
        params: { userAccessKey: state.userAccessKey },
        data: { productId },
        baseURL: API_BASIC_URL,
      })
      .then((response) => {
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, response.data.items);
      })
      .catch(() => commit(MutationTypes.SYNC_CART_PRODUCTS));
  },
};

export const getters: GetterTree<State, State> & Getters = {
  orderInfo: (state) => {
    return state.orderInfo;
  },
  cartDetailProducts: (state) => {
    return state.cartProductsData;
  },
  cartTotalPrice(state, getters) {
    return getters.cartDetailProducts.reduce(
      (acc, item) => acc + (item.product.price || 0) * item.quantity,
      0
    );
  },
  cartTotalCount(state) {
    return state.cartProducts.reduce((acc, item) => acc + item.amount, 0);
  },
  cartProductsCount(state) {
    return state.cartProducts.length;
  },
  isCartLoading(state) {
    return state.cartLoad;
  },
  isCartLoadingFailed(state) {
    return state.cartLoadFailed;
  },
};

export const store = createStore({
  state,
  mutations,
  getters,
  actions,
  plugins: [createLogger()],
}) as CustomStore;

export const useStore = () => {
  return store;
};
