type FullNameIncorrect = string;
type AddressIncorrect = string;
type PhoneIncorrect = string;
type EmailIncorrect = string;
type CommentToOrderIncorrect = string;

export type OrderFormErrorsType = {
  name: FullNameIncorrect;
  address: AddressIncorrect;
  phone: PhoneIncorrect;
  email: EmailIncorrect;
  comment: CommentToOrderIncorrect;
};

export class OrderFormErrors implements OrderFormErrorsType {
  name: FullNameIncorrect;
  address: AddressIncorrect;
  phone: PhoneIncorrect;
  email: EmailIncorrect;
  comment: CommentToOrderIncorrect;

  constructor() {
    this.name = "";
    this.address = "";
    this.phone = "";
    this.email = "";
    this.comment = "";
  }
}
