
import CartIndicator from "@/components/Cart/CartIndicator.vue";
import { defineComponent } from "vue";
import { useStore } from "@/store";
import { ActionTypes, MutationTypes } from "@/types/store.type";
import { mapActions, mapMutations } from "vuex";
import { NAMES } from "@/others/names";

export default defineComponent({
  setup() {
    const store = useStore();
    return { store };
  },
  components: { CartIndicator },
  name: "App",
  created() {
    const userAccessKey = localStorage.getItem(NAMES.USER_ACCESS_KEY);
    if (userAccessKey) {
      this[MutationTypes.UPDATE_USER_ACCESS_KEY](userAccessKey);
    }
    this[ActionTypes.LOAD_CART]();
  },
  methods: {
    ...mapActions([ActionTypes.LOAD_CART]),
    ...mapMutations([MutationTypes.UPDATE_USER_ACCESS_KEY]),
  },
});
