type FullName = string;
type Address = string;
type Phone = string;
type Email = string;
type CommentToOrder = string;

export type OrderFormType = {
  name: FullName;
  address: Address;
  phone: Phone;
  email: Email;
  comment: CommentToOrder;
};

export class OrderForm implements OrderFormType {
  name: FullName;
  address: Address;
  phone: Phone;
  email: Email;
  comment: CommentToOrder;

  constructor() {
    this.name = "";
    this.address = "";
    this.phone = "";
    this.email = "";
    this.comment = "";
  }
}
