
import ProductList from "@/components/Product/ProductList.vue";
import BasePagination from "@/components/Base/BasePagination.vue";
import ProductFilter from "@/components/Product/ProductFilter.vue";
import { defineComponent } from "vue";
import type { ApiItems } from "@/types/productApi";
import axios from "axios";
import { API_BASIC_URL } from "@/others/basic-url";

export default defineComponent({
  setup() {
    return { loadProductsTimer: 0 };
  },
  name: "MainPage",
  components: { ProductList, BasePagination, ProductFilter },
  props: { pageParams: { type: Object, default: () => ({}) } },
  data() {
    return {
      query: {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategory: 0,
        filterColor: 0,
        page: 1,
        productsPerPage: 3,
      },
      ...this.pageParams,
      productsLoading: false,
      productsLoadingFailed: false,
      productsData: null as null | ApiItems,
    };
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get<ApiItems>("api/products", {
            baseURL: API_BASIC_URL,
            params: {
              page: this.query.page,
              limit: this.query.productsPerPage,
              colorId: this.query.filterColor,
              minPrice: this.query.filterPriceFrom,
              maxPrice: this.query.filterPriceTo,
              categoryId: this.query.filterCategory,
            },
          })
          .then((e) => (this.productsData = e.data))
          .catch(() => (this.productsLoadingFailed = true))
          .then(() => (this.productsLoading = false));
      }, 0);
    },
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    query: {
      deep: true,
      handler: "loadProducts",
      immediate: true,
    },
  },
});
