
import BaseColors from "@/components/Base/BaseColors.vue";
import { defineComponent } from "vue";
import type { ApiCategories, ApiColors } from "@/types/productApi";
import axios from "axios";
import { API_BASIC_URL } from "@/others/basic-url";

export default defineComponent({
  name: "ProductFilter",
  props: ["priceFrom", "priceTo", "categoryId", "colorId"],
  components: { BaseColors },
  setup() {
    return { loadCategoriesTimer: 0, loadColorsTimer: 0 };
  },
  data() {
    return {
      currentPriceFrom: this.priceFrom || 0,
      currentPriceTo: this.priceTo || 0,
      currentCategory: this.categoryId || 0,
      currentColorId: this.colorId || 0,
      colorsData: null as null | ApiColors,
      categoriesData: null as null | ApiCategories,
    };
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategory = value;
    },
    colorId(value) {
      this.currentColorId = value;
    },
  },
  methods: {
    loadCategories() {
      clearTimeout(this.loadCategoriesTimer);
      this.loadCategoriesTimer = setTimeout(() => {
        axios
          .get<ApiCategories>("api/productCategories", {
            baseURL: API_BASIC_URL,
          })
          .then((response) => (this.categoriesData = response.data));
      }, 0);
    },
    loadColors() {
      clearTimeout(this.loadColorsTimer);
      this.loadColorsTimer = setTimeout(() => {
        axios
          .get<ApiColors>("api/colors", { baseURL: API_BASIC_URL })
          .then((response) => (this.colorsData = response.data));
      }, 0);
    },
    changeValue(toZero: boolean) {
      this.$emit("update:priceFrom", toZero ? 0 : this.currentPriceFrom);
      this.$emit("update:priceTo", toZero ? 0 : this.currentPriceTo);
      this.$emit("update:categoryId", toZero ? 0 : this.currentCategory);
      this.$emit("update:colorId", toZero ? 0 : this.currentColorId);
    },
    submit() {
      this.changeValue(false);
    },
    reset() {
      this.changeValue(true);
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
});
