
import type { PropType } from "vue";
import { defineComponent } from "vue";
import numFormat from "@/helpers/numberFormat";
import { useStore } from "@/store";
import { ActionTypes } from "@/types/store.type";
import { mapActions } from "vuex";
import type { CartProduct } from "@/types/productType";

export default defineComponent({
  setup() {
    const store = useStore();
    return { store };
  },
  props: {
    item: {
      type: Object as PropType<CartProduct>,
      default: () => ({ amount: 0 }),
    },
  },
  name: "CartItem",
  computed: {
    amount: {
      get() {
        return this.item.quantity;
      },
      set(value: number) {
        this.store.dispatch(ActionTypes.UPDATE_CART_PRODUCT_AMOUNT, {
          productId: this.item.id,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: ActionTypes.DELETE_CART_PRODUCT_AMOUNT }),
    increment() {
      if (this.item.quantity) {
        this.amount++;
      }
    },
    decrement() {
      if (this.item?.quantity && this.item.quantity > 1) {
        this.amount--;
      }
    },
    numberFormat(value: number) {
      return numFormat(value);
    },
  },
});
