
import { computed, defineComponent } from "vue";
import { useStore } from "@/store";
import numFormat from "@/helpers/numberFormat";
import OrderItem from "@/components/Order/OrderItem.vue";
import { OrderForm } from "@/types/OrderPage.form";
import { OrderFormErrors } from "@/types/OrderPage.form.errors";
import BaseFormTextarea from "@/components/Base/BaseFormTextarea.vue";
import BaseFormText from "@/components/Base/BaseFormText.vue";
import { MutationTypes } from "@/types/store.type";
import type { AxiosResponse } from "axios";
import axios from "axios";
import type { ApiOrder } from "@/types/productApi";
import { API_BASIC_URL } from "@/others/basic-url";
import type { OrderPost } from "@/types/configsType";

export default defineComponent({
  name: "OrderPage",
  components: {
    BaseFormText,
    OrderItem,
    BaseFormTextarea,
  },
  data() {
    return {
      formData: new OrderForm(),
      formError: new OrderFormErrors(),
      formErrorMessage: "",
      isLoading: false,
    };
  },
  setup() {
    const store = useStore();
    const price = computed(() => store.getters.cartTotalPrice);
    const products = computed(() => store.getters.cartDetailProducts);
    const count = computed(() => store.getters.cartTotalCount);
    return { store, price, products, count };
  },
  methods: {
    numberFormat(value: number): string {
      return numFormat(value);
    },
    order() {
      this.isLoading = true;
      this.formErrorMessage = "";
      this.formError = new OrderFormErrors();
      axios
        .post<ApiOrder, AxiosResponse<ApiOrder>, OrderPost>(
          "api/orders",
          this.formData,
          {
            baseURL: API_BASIC_URL,
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit(MutationTypes.RESET_CART);
          this.$store.commit(MutationTypes.UPDATE_ORDER_INFO, response.data);
          this.$router.push({
            name: "orderInfo",
            params: { id: response.data.id },
          });
        })
        .catch(({ response }) => {
          const error = response.data.error;
          this.formErrorMessage = error.message;
          if (response.status === 400) {
            this.formError = Object.assign(
              new OrderFormErrors(),
              error.request || {}
            );
          }
        })
        .then(() => (this.isLoading = false));
    },
  },
});
