
import { defineComponent } from "vue";
import { ActionTypes } from "@/types/store.type";
import numFormat from "@/helpers/numberFormat";
import OrderItem from "@/components/Order/OrderItem.vue";

export default defineComponent({
  name: "OrderInfoPage",
  components: { OrderItem },
  data() {
    return { productLoading: false };
  },
  methods: {
    numberFormat(value: number): string {
      return numFormat(value);
    },
  },
  computed: {
    order() {
      return this.$store.state.orderInfo;
    },
  },
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id.toString() === this.$route.params.id
    )
      return;
    this.productLoading = true;
    this.$store
      .dispatch(ActionTypes.LOAD_ORDER_INFO, this.$route.params.id)
      .catch(() => undefined)
      .then(() => (this.productLoading = false));
  },
});
