
import BaseColors from "@/components/Base/BaseColors.vue";
import { defineComponent } from "vue";
import { ActionTypes } from "@/types/store.type";
import { useRoute } from "vue-router";
import type { Category, Color, ProductType } from "@/types/productType";
import { useStore } from "@/store";
import axios from "axios";
import { API_BASIC_URL } from "@/others/basic-url";
import { mapActions } from "vuex";

export default defineComponent({
  setup() {
    const store = useStore();
    const route = useRoute();
    return { route, store };
  },
  data() {
    return {
      productInfo: {
        productAddedFailed: false,
        productAddSending: false,
        productAdded: false,
      },
      currentColor: 0,
      productData: null as null | ProductType,
      productAmount: 1,
      loadProductTimer: 0,
      productLoading: false,
      productLoadingFailed: false,
    };
  },
  computed: {
    product(): ProductType | null {
      return this.productData;
    },
    category(): Category | undefined {
      return this.productData?.category;
    },
    currColors(): Array<Color> | undefined {
      return this.productData?.colors;
    },
    price(): number | undefined {
      return this.productData?.price;
    },
  },
  name: "ProductPage",
  components: { BaseColors },
  methods: {
    ...mapActions([ActionTypes.ADD_PRODUCT_TO_CART]),
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios
          .get<ProductType>(`api/products/${this.route.params.id}`, {
            baseURL: API_BASIC_URL,
          })
          .then((e) => (this.productData = e.data))
          .catch(() => (this.productLoadingFailed = true))
          .then(() => (this.productLoading = false));
      }, 0);
    },
    addToCart() {
      if (this.product) {
        if (this.productAmount < 1) return;
        this.productInfo.productAddedFailed = false;
        this.productInfo.productAddSending = true;
        this.productInfo.productAdded = false;
        this[ActionTypes.ADD_PRODUCT_TO_CART]({
          productId: this.product.id,
          amount: this.productAmount,
        })
          .then(() => (this.productInfo.productAdded = true))
          .catch(() => (this.productInfo.productAddedFailed = true))
          .then(() => (this.productInfo.productAddSending = false));
      }
    },
    increment() {
      this.productAmount++;
    },
    decrement() {
      if (this.productAmount > 1) {
        this.productAmount--;
      }
    },
  },
  watch: {
    "route.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
});
