
import type { PropType } from "vue";
import { defineComponent } from "vue";
import type { Color } from "@/types/productType";

export default defineComponent({
  name: "BaseColors",
  props: {
    color: Number,
    colors: Object as PropType<Array<Color>>,
  },
  watch: {
    color(value: number) {
      this.currentColor = value;
    },
  },
  computed: {
    currentColor: {
      get() {
        return this.color;
      },
      set(value: number) {
        this.$emit("update:color", value);
      },
    },
  },
});
